function Status(params) {
    if (params.isOnline === true) {
        return (
            <section class="fixed right-2 bottom-2 bg-white px-5 rounded">
                <p>
                    Node is Online
                </p>
            </section>
        )
    } else {
        return (
            <section class="fixed right-2 bottom-2 bg-white px-5 rounded">
                <p>
                    Node is Offline
                </p>
            </section>
        )
    }
}

export default Status