import ChartPlot from './ChartPlotter'
import GET from '../components/backend'
import { useEffect, useState } from 'react'
import Status from './Status'

function ChartTab(props) {
    var Ranges = props.Ranges
    var [labels, setlabels] = useState([])
    var [CO2_data, setCo2] = useState([])
    var [VOC_data, setVOC] = useState([])
    var [Temperature_data, setTemperature] = useState([])
    var [Humidity_data, setHumidity] = useState([])
    var [PM2_5_data, setPM2_5] = useState([])
    var [PM10_data, setPM10] = useState([])

    var [isLatest, setisLatest] = useState(false)
    const hook = () => {
        GET.getDataMany().then((response) => {
            // console.log("Hooking\n");
            setlabels(response.labels)
            setCo2(response.CO2)
            setVOC(response.VOC)
            setTemperature(response.Temperature)
            setHumidity(response.Humidity)
            setPM2_5(response.PM2_5)
            setPM10(response.PM10)
            setisLatest(response.isLatest)
        })
    }
    useEffect(hook, [])

    if (props.page !== 1) {
        return (<></>)
    } else {
        return (
            <>
                <section>
                    <div class="container px-5 py-24 mx-auto">
                        <div class="flex flex-wrap -m-4">
                            <ChartPlot labels={labels} title="CO2" data={CO2_data} range={Ranges.CO2}></ChartPlot>
                            <ChartPlot labels={labels} title="VOC" data={VOC_data} range={Ranges.VOC}></ChartPlot>
                            <ChartPlot labels={labels} title="Temperature" data={Temperature_data} range={Ranges.Temperature}></ChartPlot>
                            <ChartPlot labels={labels} title="Humidity" data={Humidity_data} range={Ranges.Humidity}></ChartPlot>
                            <ChartPlot labels={labels} title="PM2_5" data={PM2_5_data} range={Ranges.PM2_5}></ChartPlot>
                            <ChartPlot labels={labels} title="PM10" data={PM10_data} range={Ranges.PM10}></ChartPlot>
                        </div>
                    </div>
                </section>
                <Status isOnline={isLatest}></Status>
            </>
        )
    }
}

export default ChartTab