import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, Title, Tooltip, LineElement, Legend, CategoryScale, LinearScale, PointElement, Filler } from 'chart.js';
ChartJS.register(
  Title, Tooltip, LineElement, Legend,
  CategoryScale, LinearScale, PointElement, Filler
)

function ChartPlot(params) {
  // console.log("entry", params)
  const data = {
    title: {
      text: "Hello"
    },
    labels: params.labels,
    datasets: [
      {
        data: params.data,
        tension: 0,
        fill: true,
        pointBorderColor: 'rgb(255,0,0)',
        pointBackgroundColor: 'rgb(239,0,0)',
        showLine: true,
        radius: 1.5,
        // borderColor: "#000",
        linecolor: "#fff",
        backgroundColor: "rgba(239,0,0,0.7)"
      }
    ]
  }
  const options = {
    plugins: {
      title: {
        display: true,
        text: params.title,
        padding: 15,
        font: {
          size: 20
        }
      },
      legend: {
        display: false
      }
    },
    scales: {
      y: {
        min: params.range.min,
        max: params.range.max
      }
    }
  }
  // console.log("exit", data);
  return (
    <div class="px-4 py-5 lg:py-0 lg:w-1/3 sm:w-full lg:mb-10 sm:mb-1">
      <div class="h-full bg-gray-100 px-8 pt-5 pb-4 rounded-lg text-center relative shadow-lg">
        <div className="graph">
          <Line data={data} options={options}></Line>
        </div>
      </div>
    </div>
  );
}

export default ChartPlot;